* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  background-image: -webkit-repeating-radial-gradient(
    center center,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.2) 1px,
    transparent 1px,
    transparent 100%
  );
  background-size: 20px 20px;
}

#search-box {
  font: inherit;
  padding: 8px;
  width: 320px;
  margin-bottom: 16px;
  margin-left: 20px;
}

.country-list {
  list-style: none;
  padding-left: 0;
  margin-left: 20px;
}

.country-list .item {
  display: flex;
  align-items: center;
  gap: 10px;
}

.country-info {
  margin-left: 20px;
}

.country-info .country-name-with-flag {
  display: flex;
  align-items: center;
  gap: 10px;
}

.country-info .official-name {
  margin: 0;
  font-size: 30px;
}

.country-info p {
  font-weight: 700;
}

.country-info span {
  font-weight: 400;
}
